@import "variables";
.wrapper{
    margin: 0px 5%;
}
.page{
    margin: 0px;
    padding: 0px;
    font-family: 'Roboto', sans-serif;
}
.header{
    text-align: center;
    background-color: #000;
}
.menu{
    height: 100px;
    padding: 0px;
    margin: 0px;
    list-style: none;
    &__row{
        vertical-align: center;
        display: inline-block;
    }
    &__link{
        padding: 40px 40px;
        color: #78858e;
        text-decoration: none;
        font-size: 16px;
        text-transform: uppercase;
        &-active{
            color: #fff;
        }
    }
    &__logo{
        position: relative;
        top: 15px;
    }
}
.slider{
    color: #fff;
    text-align: center;
    height: 750px;
    background-image: url(../img/slider-image.png);
    &__title{
        padding-top: 230px;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 59px;
    }
    &__explanation{
        font-size: 20px;
    }
}
.info{
    height: 580px;
    &_content{
        padding-top: 150px;
        padding-left: 70px;
        float: left;
        width: 50%;
        &__title{
            color: #f16522;
            font-size: 50px;
        }
        &__button{
            display: inline-block;
            text-decoration: none;
            text-transform: uppercase;
            color: #fff;
            background-color: #f16522;
            padding: 15px 20px;
            font-size: 18px;
        }
    }
    &__image{
        width: 30%;
        min-height: 600px;
        background-repeat: no-repeat;
        background-image: url(../img/chef.png);
        background-position: 50% 35%;
        padding-top: 50px;
        padding-right: 70px;
        float: right;
    }

}

